export default {
  //
  startLoading(state) {
    state.loading = true;
  },
  //
  overLoading(state){
    state.loading = false;
  },
  //
  noMoreData(state){
    state.noMore = true;
  },
  moreData(state){
    state.noMore = false;
  },
  setCurrent(state,param){
    state.currentSection = param;
  }
};
