import { getUserInfo } from "@/api/api";
const getUserData = () => {
  try {
    return JSON.parse(sessionStorage.getItem("userInfo")) || {};
  } catch (error) {
    return {};
  }
};
const state = {
  //
  auth: {
    //
    loggedIn() {
      if (Object.keys(state.userData).length > 0) {
        //TODO:
        if (true) {
          return { result: 1, data: 0, msg: "" };
        } else {
          return { result: 0, data: 0, msg: "!" };
        }
      } else {
        return { result: 0, data: 0, msg: "" };
      }
    }
  },
  //
  userData: getUserData()
};
const getters = {
  //
  getLoggedIn(state) {
    return state.auth.loggedIn();
  },
  //
  getUserData(state) {
    return state.userData;
  }
};
const mutations = {
  setUserData(state, res) {
    state.userData = res;
  }
};
const actions = {
  //
  setUserData(context, param) {
    return getUserInfo(param).then(res => {
      sessionStorage.setItem("userInfo", res);
      context.commit("setUserData", res);
    });
  },
  //
  clearUserData(context) {
    sessionStorage.removeItem("userInfo");
    context.commit("setUserData", {});
  }
};
export default {
  state,
  getters,
  mutations,
  actions
};
