<template>
  <div class="login">
    <van-cell-group>
      <van-field
        v-model="phone"
        label="+86"
        placeholder=""
        :error-message="errorPhone"
        @input="checkPhone"
        maxlength="11"
      />
      <van-field
        v-model="sms"
        center
        clearable
        label=""
        placeholder=""
        :error-message="errorMsm"
      >
        <van-button slot="button" size="small" type="primary"></van-button>
      </van-field>
    </van-cell-group>
    <div class="login_text">
      <p></p>
    </div>
    <div class="login_btn">
      <van-button type="info" round size="large" @click="login"></van-button>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";
export default {
  name: "login",
  data() {
    return {
      phone: "",
      sms: "",
      errorPhone: "",
      errorMsm: ""
    };
  },
  methods: {
    ...mapActions(["setUserData"]),
    //
    checkPhone() {
      if (!this.phone) {
        this.errorPhone = "";
      } else {
        this.errorPhone = "";
        if (!this.phone.match(/1[^0-2][0-9]{9}/)) {
          this.errorPhone = "";
        }
      }
    },
    login() {
      if (this.phone != "" && this.sms != "") {
        //
        if(this.errorPhone == ""){
          //TODO: ,
          this.setUserData({ phone: this.phone, sms: this.sms }).then(() => {
            //
            const redirect = decodeURIComponent(
              this.$router.history.current.query.redirect || "/"
            ); //queryhistorycurrent
            this.$router.push({ path: redirect });
          });
        }
      } else {
        //
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.login {
  margin-top: 0.2rem;

  .login_text {
    color: #666;
    text-align: center;
    line-height: 0.4rem;
  }

  .login_btn {
    margin-top: 0.2rem;
  }
}
</style>